.sidebar{

  position: fixed;
  width: 15rem;
  height:100%;
  background-color: darken(#0d88e6, 20%);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  margin-top: -1.2rem;
  @media screen and (max-width:$medium){
    width: 15rem;
  }

  #close{
    display: none;
  }

.brand {
    color: white;
    font-size: 1.6rem;
    display: flex;
    flex-direction: column;
    gap: 0.9rem;
    justify-content: center;
    align-items: center;
    margin: 2rem 0;
    font-weight: 900;


  img{
    width: 4rem;
    height:2.8rem;
    margin-right: 0.6rem;
  }
}


.links {

  ul { margin-bottom: 3rem;

    .active {
      border-right: 0.3rem solid white;

      a{
        color: white;

      }
    }

    li{border-right: 0.3rem solid transparent;
      margin: 1rem 0;
      list-style-type: none;

      a{
        margin: 0 2rem;
        text-decoration: none;
        color: white;
        font-size: 1.2rem;
        display: flex;
        gap: 1rem;

      }

      transition: 0.3s ease-in-out;
      &:hover {
        border-right-color:white;

        a{
          color: #4CB5F5;
        }
      }

    }

  }
}

.pro {
  color: white;
  background-color: #4CB5F5;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap:0.6rem;
  margin: 0 2rem;
  border-radius: 2rem;
  padding: 1rem;
  text-align: center;

  .image{

    img{
      height:2rem;
    }
  }

    button{
      color:#1C4E80;
      font-weight: 900;
      padding: 0.5rem 1rem;
      border:none;
      border-radius: 2rem;
      cursor: pointer;
    }

  }

  @media screen and (max-width:$small) {
    left: -16rem;
    transition: 1s all ease-in-out;
    z-index: 999;
    #close{
      display: block;
      position: absolute;
      color: #fff;
      top: 0.4rem;
      left: 12rem;
      font-size: 35px;
      font-weight: bolder;
    }
  }

} /*MAIN */
