@mixin getMilestonesStyles(){

  .milestones{

    display:flex;
    justify-content:space-between;
    background-color: darken(#0d88e6, 20%);
    padding: 0.8rem;
    border-radius: 1rem;
    .milestone{
      display: flex;
      justify-content: center;
      align-items: center;
      gap:1rem;
      width: 25%;
      border-right: 3px solid #0d88e6;
      padding-left: 5px;
      &:last-of-type{
        border-right: none;
      }
      padding-right: 3rem;
      .icon{
        padding: 1rem;
        font-size:1.4rem;
        border-radius: 1rem;
        color:white;
        background-color:#0d88e6;
      }
      h2{
        color:lighten(#0d88e6, 10%);
      }

      @media screen and (max-width:$medium){
        width: 50%;
        margin-top: 10px;
        justify-content: space-between;
        padding-left: 15px;
      }
      @media screen and (max-width:$small){
        width: 100%;
        border: none;
        border-bottom: 1px solid grey;
        padding: 0px;
        padding-bottom: 10px;
      }
    }
    @media screen and (max-width:$medium) {
      flex-wrap: wrap;
      justify-content: space-evenly;
      :nth-child(2){
        border-right: none;
      }
    }

    @media screen and (max-width:$small){
     padding: 0px 20px;
     overflow: hidden;
    }




}

}
