@mixin getAdviceStyles(){

.advice{



$baseColor: #0061A8;
$borderRadius: 2rem;



.table-users {
   border-radius: 2rem;
   box-shadow: 3px 3px 0 rgba(0,0,0,0.1);
   overflow: hidden;
   width: 100%;
   height: 100%;
  background-color: white;



   .header {
   background-color: lighten($baseColor, 7%);
   color: white;
   font-size: 1.5em;
   padding: 0.8rem;
   text-align: center;
   text-transform: uppercase;

   h1{

      font-size:1.8rem;
      font-weight: 600;
    }

}

table {
  width: 100%;
  max-width: 100%;



   td, th {
      padding: 11px;
      text-align: center;

   }

   .newsdata{
      font-weight: 700;

    }

    .analyst{

      text-decoration: underline;
    }
   td {
      vertical-align: middle;
      color:black;
      &:nth-child(2n+1) {text-align: center; }



    a{
    text-decoration: none;
    color: black;

    &:hover {
    color: #0061A8;
    text-decoration: underline;
    cursor: pointer;
}

   }

      &:last-child {
         font-size: 0.95em;
         line-height: 1.4;
         text-align: left;
      }
   }
   th {
      font-weight: 800;
      font-size: 1.1rem;
      text-decoration: underline;
      background-color: lighten($baseColor, 53%);
      color:  #0061A8;
      &:nth-child(2n+1) {text-align: center; }



   }
   tr {
      &:nth-child(2n+1) { background-color: white; }
      &:nth-child(2n) { background-color: lighten($baseColor, 100%) }
   }


   @media screen and (max-width:$medium) {
      width: 100%;
   }

}

@media screen and (max-width:$medium) {
   margin-bottom: 40px;
}


}

  }
  @media screen and (max-width:$medium) {
     width: 100%;
  }
}
