@mixin getNewsStyles(){

.news{

      height: 100%;


$baseColor: #F76E11;
$borderRadius: 2rem;



.table-users {
   border-radius: 2rem;
   box-shadow: 3px 3px 0 rgba(0,0,0,0.1);
   overflow: hidden;
   width: 100%;
   height: 100%;
  background-color: white;



   .header {
   background-color: darken($baseColor, 5%);
   color: white;
   font-size: 1.5em;
   padding: 0.8rem;
   text-align: center;
   text-transform: uppercase;

   h1{

      font-size:1.8rem;
      font-weight: 600;
    }

}

table {
    max-width: 100%;
    width: 100%;



   td, th {
      color: darken($baseColor, 10%);
      padding: 10px;
   }

   .newsdata{
      font-weight: 700;
    }
   td {
      vertical-align: middle;
      color:black;
      &:nth-child(2n+1) {text-align: center; }


    a{
    text-decoration: none;
    color: black;

    &:hover {
    color:#F76E11;
    text-decoration: underline;
    cursor: pointer;
}

   }

      &:last-child {
         font-size: 0.95em;
         line-height: 1.4;
         text-align: left;
      }
   }
   th {
      font-weight: 800;
      font-size: 1.1rem;
      text-decoration: underline;
      background-color: lighten($baseColor, 42%);
      text-align: left;
      &:nth-child(2n+1) {text-align: center; }



   }
   tr {
      &:nth-child(2n+1) { background-color: white; }
      &:nth-child(2n) { background-color: lighten($baseColor, 100%) }
   }


   @media screen and (max-width:$medium) {
      width: 100%;
   }

}

@media screen and (max-width:$medium) {
   margin-bottom: 30px;
}


}

  }
  @media screen and (max-width:$medium) {
     width: 100%;
  }
}
