@mixin getNavbarStyles{
  .navbar{
    display: flex;
    justify-content: space-between;

    .container{
      display: flex;

      form{
        height: 100%;
        display: flex;
        flex-direction: row;

      .button {
        flex: 1;
        color: white;
        background-color: #0d88e6;
        padding: 0.2rem 1.2rem;
        border: none;
        border-radius: 1.5rem;
        font-size: 0.9rem;
        font-weight: 600;
        cursor: pointer;
      }

      .button:active {
          background-color: #1C4E80;
      }
      .search{
        background-color: white;
        display: flex;
        width:32rem;
        margin-right: 0.5rem;
        justify-content: left;
        align-items: center;
        border-radius: 2rem;
        padding: 0.5rem 1rem;
        color: #0d88e6;
        font-size: 1.5rem;
        input{
          border:none;
          color: #0d88e6;
          background-color:white;
          &:focus{
            outline:none;
          }

        }

         select{
           width: 30rem;
          border:0px;
          outline:0px;
         font-size: 1rem;
         margin-left: -9rem;
         color: darken(#1C4E80,10%);



         option{
       font-size: 1.1rem;
       font-weight: 500;
         }


         }


        @media screen and (max-width:$medium) {
         width: 50vw;
         height: 50px;
        }
        @media screen and (max-width:$small) {
          width: 60vw;
          height: 40px;
         }


       }
      @media screen and (max-width:$medium) {
        margin:auto;
        margin-top: 40px;

      }


      }
    .profile{
      display:flex;
      align-items: center;
      gap:1rem;
      margin-left: 1rem;
      .bell {
        font-size: 1.4rem;
        color: #0d88e6;
      }
      .image{
        img{
          height: 3rem;
          border-radius: 3rem;
        }
      }

      @media screen and (max-width:$medium){
        position: absolute;
        top:10px;
        right: 10px;
      }
    }
  }

  #menu{
    display: none;
  }

    @media screen and (max-width:$medium) {
      display: block;

      .title{
        width: 100%;
        display: flex;
        align-items: center;
        span{
          margin-left: 20px;
          @media screen and (max-width:860px){
            margin-left: 0px;
          }
        }
        @media screen and (max-width:860px){
          display: block;
        }
      }
    }

    @media screen and (max-width:$small) {
      #menu{
        display: inline-block;
        font-size: 40px;
      }
      .title{
        display:none;
      }
    }
  }

}
