.dashboard{
margin-left: 15rem;
padding: 0 2rem;
height: 100%;
color:white;
@include getNavbarStyles();

.grid{
  height: 100%;
  .one{
    margin:2rem 0;
    @include getMilestonesStyles();

  }
  .two{
    height: 100%;
    display:grid;
    justify-content: center;
    grid-template-columns: 0.5fr 1.0fr;
    gap: 1rem;
    @include getTwitterStyles();
    @include getLstmStyles();
    @media screen and (max-width:$medium) {
      display: block;
      height: max-content;
    }

  }

  .three {
    margin-top: 1.2rem;
    height: 100%;
    display: grid;
    gap: 1rem;
    @include getIndicatorsStyles();

    @media screen and (max-width:$small) {
      overflow-x: scroll;
    }

  }

  .four{

    margin-top: 1.2rem;
    height: 100%;
    display: grid;
    grid-template-columns: 1.0fr 1.0fr;
    gap: 1rem;
    @include getNewsStyles();
    @include getAdviceStyles();
    @media screen and (max-width:$medium) {
      display: block;
    }

  }


  }

  @media screen and (max-width:$small) {
   margin-left: 0rem;

  }
}
