@mixin getIndicatorsStyles(){



  .indicators{


  height: 100%;

$baseColor: #0061A8;
$borderRadius: 2rem;


.table-users {
   border-radius: $borderRadius;
   box-shadow: 3px 3px 0 rgba(0,0,0,0.1);
   overflow: hidden;
   width: 100%;
   height: 100%;
  background-color: white;

   .header {
   background-color: darken($baseColor, 1%);
   color: white;
   padding: 0.8rem;
   text-align: center;
   text-transform: uppercase;
   font-size: 1.7em;
    font-weight: 600;



   h1{

      font-size:1.8rem;
      font-weight: 600;
    }

}

table {
    width: 100%;



   td, th {
      padding: 11px;
      text-align: center;
   }


   td {
      vertical-align: middle;
      color:black;
      font-weight: 500;




      &:last-child {
         text-align: center;

      }


   }

   .indic{
        font-weight: 700;
      }

      .action{
      font-weight: 700;
    }

    .summary{

      font-weight: 900;
      font-size: 1.1rem;
    }







   th {
      font-weight: 800;
      font-size: 1.1rem;
      text-decoration: underline;
      text-align: center;
      color: darken($baseColor, 10%);

   }

   tr {
      &:nth-child(2n+1) { background-color: white; }
      &:nth-child(2n) { background-color: lighten($baseColor, 63%) }
   }
}

}

.gauge{
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      padding: 0rem 2rem;
      background-color: lighten(#0061A8, 60%);


      @media screen  and (max-width:760px) {
        flex-direction: column;
        height:max-content;

        .text{

          margin-bottom: 2rem;
          margin-right: 4rem;
          margin-top: 1rem;
        }


        .mov{

          margin-right:3rem;
        }
      }


    .text{
      display: flex;
      flex-direction: column;
      gap: 0.5rem;
      margin-left: 1rem;


      #result{

        font-size:1.35rem;
        color: gray;
      }



    }

    .mov{

      margin-left: 5rem;

      }

     p{
      color: #0061A8;
      font-weight: 800;
      align-items: center;
      text-align: center;
    }



    .chart1{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;

    }
    .chart2{
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;

    }

    }


  }
}
